/*eslint-disable*/
import React, { useState, useEffect } from 'react';
import { BsFillMicFill, BsArrowClockwise } from 'react-icons/bs';
import './card.scss';
import * as Sounds from '../../../../resources/audio';
import * as Imgs from '../../../../resources/img';
import { useTypedSelector } from '../../../../hooks/useTypedSelector';

type Card = {
  card: any,
  click: any,
};

const Card = ({ card, click }: Card): JSX.Element => {
  const { word, translation, image, audioSrc } = card;
  const { mode } = useTypedSelector((state) => state.mode);
  const [flip, setFlip] = useState(false);
  const aud = Object.values(Sounds).find((element) => element.includes(word));
  const audio = new Audio(aud);
  const start = () => {
    audio.play();
  };

  const revertCard = () => setFlip(!flip);

  const test = () => { flip ? setFlip(!flip) : '' };
  const res = Object.values(Imgs).find((element) => element.includes(word));
  if (mode === 'train') {
    return (
      <div className={flip ? "card-container flipped" : "card-container"} onMouseLeave={test} >
        <div className="card">
          <div className="card__front" onClick={start}>
            <div className="img-wrapper">
              <img className="card-img" src={`${res}`} alt={word} />
            </div>
            <div className="card-description">
              <div className="card-name">{word}</div>
              <div className="card-icons">
                <BsFillMicFill className="audio-icon" onClick={start} />
                <BsArrowClockwise className="revert-icon" onClick={revertCard} />
              </div>
            </div>
          </div>
          <div className="card__back">
            <div className="img-wrapper">
              <img className="card-img" src={`${res}`} alt={word} />
            </div>
            <div className="card-description">
              <div className="card-name">{translation}</div>
            </div>
          </div>
        </div>
      </div>
    )
  }
  return (
    <div className={flip ? "card-container flipped" : "card-container"} onMouseLeave={test} >
    <div className="card" onClick={() => click(word)}>
      <div className="card__front game">
        <div className="img-wrapper">
          <img className="card-img" src={`${res}`} alt={word} />
        </div>
        <div className="card-description hidden">
          <div className="card-name">{word}</div>
          <div className="card-icons">
            <BsFillMicFill className="audio-icon" onClick={start} />
            <BsArrowClockwise className="revert-icon" onClick={revertCard} />
          </div>
        </div>
      </div>
      <div className="card__back">
        <div className="img-wrapper">
          <img className="card-img" src={`${res}`} alt={word} />
        </div>
        <div className="card-description">
          <div className="card-name">{translation}</div>
        </div>
      </div>
    </div>
  </div>
  )
};

export default Card;
