/*eslint-disable*/
import { useActions } from '../../../../hooks/useActions';
import { useTypedSelector } from '../../../../hooks/useTypedSelector';
import Card from '../card/card';
import './cards.scss';
import * as Sounds from '../../../../resources/audio';
import * as Imgs from '../../../../resources/img';
import { useHistory } from 'react-router-dom';

type Category = {
category: any,
}

const shuffle = (array: any) => {
  var currentIndex = array.length,  randomIndex;

  while (0 !== currentIndex) {

    randomIndex = Math.floor(Math.random() * currentIndex);
    currentIndex--;

    [array[currentIndex], array[randomIndex]] = [
      array[randomIndex], array[currentIndex]];
  }

  return array;
}

const Cards = (cat: Category) => {
  const { category } = cat;
  const { setName, data } = category;
  const { mode } = useTypedSelector((state) => state.mode);
  const { isGameStarted } = useTypedSelector((state) => state.gameState);
  const { gameCards } = useTypedSelector((state) => state.gameState);
  const { activeCard } = useTypedSelector((state) => state.gameState);
  const { movesNum } = useTypedSelector((state) => state.movesState);
  const failureAudio = new Audio(Sounds.failureAudio);
  const correctAudio = new Audio(Sounds.correctAudio);
  const history = useHistory();
  console.log(movesNum);
  const { changeGameState } = useActions();
  const { changeGameCards } = useActions();
  const { failures } = useTypedSelector((state) => state.movesState);
  const { changeMovesNumber } = useActions();
  const { changeActiveCard } = useActions();
  const { changeFailuresNumber } = useActions();
  const arr: (string | undefined)[] = [];
  for (let i = 0 ; i < cat.category.data.length; i++) {
    arr.push(Object.values(Sounds).find((element) => element.includes(cat.category.data[i].word)));
  }

  const startGame = () => {
    const shuffledArr = shuffle(arr);
    changeGameState();
    changeGameCards(shuffledArr);
    changeActiveCard(shuffledArr[0]);
    const audio = new Audio(shuffledArr[0]);
    audio.play();
  };

  const game = (clickedCard: string) => {
    changeMovesNumber();
    if(isGameStarted) {
    if (activeCard.includes(clickedCard)) {
      correctAudio.play();
      const indexOfNext = gameCards.indexOf(activeCard) + 1;
      if(indexOfNext < 8) {
      const nextAudio = gameCards[indexOfNext];
      changeActiveCard(nextAudio);
      playNext(nextAudio);
      } else {
        history.push('/final');
        // location.href = '/final';-
      
      }
    } else {
      changeFailuresNumber();
    failureAudio.play();
    }
  }
  };

  const playNext = (nextAudio: string) => {
    const next = new Audio(nextAudio);
    next.play();
      };

  const repeatSound = () => {
    const currentAudio = new Audio(activeCard);
    currentAudio.play();
  }

  return (
    <div className="cards-page">
       <h2>{setName}</h2>
      <div className="cards-field">
     { data.map((card: any, idx: number) => {
       return <Card key={idx} card={card} click={game} />
  })
}
    </div>
    { mode === 'game' && !isGameStarted && <button type="button" onClick={startGame}>
       Start
       </button>
     } 
      { mode === 'game' && isGameStarted &&  
      <div className="game-icons">
      <button type="button" onClick={repeatSound}>
       Resume
       </button>
       <div className="icons">Icons here
       <img src={Imgs.emptyHeartPic}/></div>
       </div>
     } 
    </div>
  );
};

export default Cards;
