import './category.scss';
import { Link } from 'react-router-dom';
import * as Imgs from '../../../../resources/img';
import { useTypedSelector } from '../../../../hooks/useTypedSelector';

type Category = {
    category: string,
    path: string,
    data: any,
};

const Category = ({ category, path, data }: Category): JSX.Element => {
  const { mode } = useTypedSelector((state) => state.mode);
  const {
    word,
  } = data[6];
  const res = Object.values(Imgs).find((element) => element.includes(word));
  if (mode === 'train') {
    return (
      <Link to={path} className="category-link">
        <div className="category">
          <div className="img-wrapper">
            <img className="card-img" src={`${res}`} alt={word} />
          </div>
          <div className="card-description">
            {category}
          </div>
        </div>
      </Link>
    );
  }
  return (
    <Link to={path} className="category-link">
      <div className="category game-mode">
        <div className="img-wrapper">
          <img className="card-img" src={`${res}`} alt={word} />
        </div>
        <div className="card-description">
          {category}
        </div>
      </div>
    </Link>
  );
};

export default Category;
