import React, { useState } from 'react';
import Category from '../category/category';
import './categoryConteiner.scss';

type Cat = {
  setName: string,
  setPath: string,
  data: any,
}
const CategoryContainer = (cards: any) => {
  // eslint-disable-next-line react/destructuring-assignment
  const cardSet = cards.cards;
  return (
    <div className="categories-container">
      {cardSet.map((card: Cat, index: React.Key | null | undefined) => {
        const { setName, setPath, data } = card;
        return (
          <Category
            // eslint-disable-next-line react/no-array-index-key
            key={index}
            category={setName.toString()}
            path={setPath}
            data={data}
          />
        );
      })}
    </div>
  );
};

export default CategoryContainer;
